import { ReactElement, RefObject, useEffect } from "react";
import { ngon, translate, Cubic, multiply, add } from "../../geometry";

export const useNgon = (sides: number, scale:number, rotation: number, cornerRadius: number, strokeWidth: number): [number[][], Cubic, number] => {
	const [ssw, scr] = multiply([strokeWidth, cornerRadius], scale);
	const radius = scale/2;
	const [cpts, vb] = ngon(sides, radius, rotation, scr, [0, 0]);
	const hsw = ssw/2;
	const bvb: Cubic = add(vb, [-hsw, -hsw, ssw, ssw]) as Cubic;
	const points = translate(cpts, [-bvb[0], -bvb[1], -bvb[0], -bvb[1]])
	return [points, [0, 0, bvb[2], bvb[3]], ssw];
}

export const useIcon = (icon: ReactElement, ref: RefObject<SVGGElement>) => useEffect(()=>{
	const elems = (ref.current! as SVGGElement).children
	if (elems.length === 0 || elems[0].tagName !== 'svg') return; //do nothing because I wasnt trained for this
	const elem = elems[0];
	elem.removeAttribute('width');
	elem.removeAttribute('height');
}, [icon])